/* timeline */
.cdc-timeline {
  list-style: none;
  padding: 20px 0 20px;
  position: relative;
}

.cdc-timeline:before {
  top: 0;
  bottom: 0;
  position: absolute;
  content: " ";
  width: 3px;
  background-color: #c0e9ff;
  left: 50%;
  margin-left: -1.5px;
}

.cdc-timeline > li {
  margin-bottom: 20px;
  position: relative;
  width: 50%;
  float: left;
  clear: left;
}

.cdc-timeline > li:before, .cdc-timeline > li:after {
  content: " ";
  display: table;
}

.cdc-timeline > li:after {
  clear: both;
}

.cdc-timeline > li:nth-child(2) {
  margin-top: 60px;
}

.cdc-timeline > li .timeline-panel {
  width: 95%;
  float: left;
  border: 1px solid #d4d4d4;
  position: relative;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.23);
  border-radius: .25rem;
}

.cdc-timeline > li .timeline-panel:before {
  position: absolute;
  top: 26px;
  right: -15px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-left: 15px solid #ccc;
  border-right: 0 solid #ccc;
  border-bottom: 15px solid transparent;
  content: " ";
}

.cdc-timeline > li .timeline-panel:after {
  position: absolute;
  top: 27px;
  right: -14px;
  display: inline-block;
  border-top: 14px solid transparent;
  border-left: 14px solid #fff;
  border-right: 0 solid #fff;
  border-bottom: 14px solid transparent;
  content: " ";
}

.cdc-timeline > li .timeline-heading img {
  border-top-left-radius: 0.25rem !important;
  border-top-right-radius: 0.25rem !important;
}

.cdc-timeline > li .timeline-badge {
  color: #fff;
  width: 24px;
  height: 24px;
  line-height: 50px;
  font-size: 1.4em;
  text-align: center;
  position: absolute;
  top: 20px;
  right: -12px;
  z-index: 100;
}

.cdc-timeline > li .timeline-badge a {
  color: #005eaa !important;
}

.cdc-timeline > li .timeline-badge a:hover {
  color: blue !important;
}

.cdc-timeline > li.timeline-inverted {
  float: right;
  clear: right;
  margin-top: 30px;
  margin-bottom: 30px;
}

.cdc-timeline > li.timeline-inverted > .timeline-panel {
  float: right;
}

.cdc-timeline > li.timeline-inverted > .timeline-panel:before {
  border-left-width: 0;
  border-right-width: 15px;
  left: -15px;
  right: auto;
}

.cdc-timeline > li.timeline-inverted > .timeline-panel:after {
  border-left-width: 0;
  border-right-width: 14px;
  left: -14px;
  right: auto;
}

.cdc-timeline > li.timeline-inverted > .timeline-badge {
  left: -12px;
}

.cdc-timeline > li .timeline-title {
  margin: .75rem 1rem;
  color: inherit;
  font-family: "Open Sans", sans-serif;
}

.cdc-timeline > li .timeline-body > p, .cdc-timeline > li .timeline-body > ul {
  padding: .75rem 1rem;
  margin-bottom: 0;
}

.cdc-timeline > li .timeline-body > p + p {
  margin-top: 5px;
}

.cdc-timeline > li .timeline-footer {
  padding: 1rem;
  background-color: #f4f4f4;
}

.cdc-timeline > li .timeline-footer a {
  cursor: pointer;
  text-decoration: none;
}

@media (max-width: 767px) {
  .cdc-timeline:before {
    left: 10px;
  }
  .cdc-timeline > li {
    margin-bottom: 20px;
    position: relative;
    width: 100%;
    float: left;
    clear: left;
  }
  .cdc-timeline > li > .timeline-panel {
    width: calc(100% - 40px);
  }
  .cdc-timeline > li > .timeline-badge {
    left: -2px;
    margin-left: 0;
  }
  .cdc-timeline > li > .timeline-panel {
    float: right;
  }
  .cdc-timeline > li > .timeline-panel:before {
    border-left-width: 0;
    border-right-width: 15px;
    left: -15px;
    right: auto;
  }
  .cdc-timeline > li > .timeline-panel:after {
    border-left-width: 0;
    border-right-width: 14px;
    left: -14px;
    right: auto;
  }
  .cdc-timeline > li.timeline-inverted {
    float: left;
    clear: left;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .cdc-timeline > li.timeline-inverted > .timeline-badge {
    left: -2px;
  }
}

