.aa-input-search::-webkit-search-results-button,
.aa-input-search::-webkit-search-results-decoration {
  display: none;
}

.aa-input-icon {
  height: 16px;
  width: 16px;
  position: absolute;
  top: 50%;
  right: 16px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  fill: #e4e4e4;
  pointer-events: none;
}

.aa-dropdown-menu {
  background-color: #fff;
  border: 1px solid rgba(228, 228, 228, 0.6);
  min-width: 300px;
  margin-top: 10px;
  box-sizing: border-box;
}

.aa-suggestion {
  padding: 6px 12px;
  cursor: pointer;
}

.aa-suggestions-category {
  border-bottom: 1px solid rgba(228, 228, 228, 0.6);
  border-top: 1px solid rgba(228, 228, 228, 0.6);
  padding: 6px 12px;
}

.aa-dropdown-menu > div {
  display: inline-block;
  width: 50%;
  vertical-align: top;
}

.aa-empty {
  padding: 6px 12px;
}

footer {
  font-size: 13px;
}

footer span {
  display: block;
}

footer ul {
  padding-left: 0px;
  list-style-type: none;
}

footer ul.list-inline {
  display: inline-block;
  margin-bottom: 3px;
}

footer ul.list-inline li {
  float: left;
  margin-left: 10px;
}

footer ul.list-inline li img {
  width: 24px;
}

footer ul.list-inline li.rounded-circle {
  background: #fff;
  padding: 7px;
  margin-right: 7px;
}
